* {
  font-family: "Fira Sans", sans-serif !important;
}
.logo {
  width: 220px;
  float: right;
  margin: 10px;
}

.container {
  height: -webkit-fill-available;
}
.even {
  background-color: #3dd;
}

.odd {
  background-color: #7ac;
}

.DB {
  height: 1000px;
  display: flex;
  height: -webkit-fill-available;
}

.weather-section {
  max-width: 100%;
  margin: auto;
  font-size: calc(0.5em + 0.1vw);
}
.weather-component {
  max-width: 100%;
  margin: 10px auto;
  text-align: left;
  background: #fff;
  border: 1px solid #ddd;
}

.city-name {
  display: block;
  text-align: center;
  margin-top: 20px;
}

.forecast-list {
  max-width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0;
}

.day-details {
  display: block;
  clear: left;
  border-top: 1px solid #ddd;
  border-radius: 2px;
}

.day-details .description {
  display: inline-block;
  padding: 10px;
}

.day-details .date {
  display: block;
}

.weather-icon {
  float: left;
  width: 50px;
  margin-top: 4px;
  margin-left: 4px;
}

.person-list {
  min-width: 800px;
}

.bar {
  max-width: 100%;
  display: flex;
  justify-content: space-between;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  -webkit-transition: 0.3s;
  transition: 0.3s;
  padding: 2px 16px;
  margin: 2vh 2vw;
}

.bar:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.bar img {
  height: 10vh;
}

.main-bar {
  background-color: #00dfac;
  color: aliceblue;
  justify-content: center;
}

.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.5);
}

.popup .popup-img {
  position: absolute;
  width: 1000px;
  height: 563px;
  left: 25%;
  right: 25%;
  top: 25%;
  bottom: 25%;
  margin: auto;
  background-color: #cccccc;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.popup .popup-img h2 {
  position: relative;
  top: 25%;
  font-size: 2.5em;
  color: white;
  text-align: center;
  vertical-align: middle;
}
