nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
nav ul li {
  background: #3dd7ac;
  box-shadow: #3dd7ac 0px 0px 0px 2px;
  font-size: 1.75em;
  line-height: 1.25em;
  padding: 1px;
  padding-left: 0px;
  margin: 16px 0px;
  width: 3px;
  border-radius: 0px 100px 100px 0px;
  -webkit-transition: width 0.2s ease, padding 0.2s ease, color 0.2s ease,
    text-shadow 0.2s ease;
  transition: width 0.2s ease, padding 0.2s ease, color 0.2s ease,
    text-shadow 0.2s ease;
}
nav ul li:hover {
  box-shadow: #3dd7ac 0px 0px 0px 2px;
  text-shadow: #000 0px 0px 2px;
}
nav ul li:hover {
  width: 90%;
  color: black;
}
nav ul li a {
  display: block;
  text-decoration: none;
  width: 220px;
  color: inherit;
  padding-left: 16px;
}

.bar {
  display: flex;
