  padding: 2px 16px;
  margin: 2vh 2vw;
}

.bar:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.bar form {
  display: flex;
  justify-content: space-between;
}
.bar input {
  margin: 0 1vw;
  margin-left: 10vw;
  margin-top: 30px;
}
.bar input[type="text"] {
  margin: 0 6vw;
  width: 30vw;
  font-size: 30px;
}
.bar input[type="text"]:disabled {
  color: #990000;
}
.main-bar {
  background-color: #3dd7ac;
  color: aliceblue;
}
.text-h2 {
  margin: 30px;
}
